.scoreboard{
    display: flex;
    justify-content: space-evenly;
    margin: 16px 0;
}

.scoreboard .score {
    color: var(--green);
    font-family: var(--signika);
    border: 2px solid var(--green);
    padding: 8px;
    border-radius: 16px;
    box-shadow: 0px 0px 45px #63c2a1;
}